



































































































































@import '../styles/colors';
@import '../styles/variables';

.header {
  color: $color-purple;
  display: flex;
  flex-direction: column;
  font-size: $font-huge;
  padding: 0;
  position: relative;
  width: 100%;
  font-weight: 500;
  padding: 0.5rem;
  margin-left: 0.5rem;
  display: none;

  @media screen and (min-width: $break-lg) {
    display: block;
  }
}

.oslo__navigation-topbar {
  display: flex;
  flex-direction: column;
  margin: 1.5rem 0.5rem 0.5rem 0.5rem;
  width: calc(100% - 1rem);
  position: relative;
  top: 0;
  background-color: $color-bg;
  z-index: 3;
  padding: 1rem 0;

  @media screen and (min-width: $break-lg) {
    padding: 2.5rem 1rem 1rem;
    width: 100%;
    margin: 0;
  }

  &-button {
    display: none;
    flex-direction: row;

    @media screen and (min-width: $break-lg) {
      display: flex;
    }
  }
}

.oslo__topbar {
  color: $color-purple;
  font-weight: bold;
  margin-bottom: 2rem;

  &-text {
    letter-spacing: 0.8px;
    margin-left: 1rem;
    margin-top: 0.3rem;
    text-transform: uppercase;
  }
}

.label {
  font-size: $font-body;
  margin-bottom: 0.25rem;
}

.topic {
  display: flex;
  align-items: center;
}

.navigation-topbar {
  width: 100%;
  max-width: 768px;
  position: relative;

  &--hidden {
    @media screen and (max-width: $break-lg) {
      display: none;
    }
  }

  &__select {
    color: $color-purple;
    display: flex;
    flex-direction: column;
    font-size: $font-huge;
    padding: 0;
    position: relative;
    width: 100%;
    font-weight: 500;
    padding: 0.5rem;

    @media screen and (min-width: $break-md) {
      padding: 1rem;
    }

    &:focus {
      outline: none;
    }

    &:hover {
      background-color: darken($color-bg, 5%);
    }
  }

  &__dropdown {
    background-color: white;
    border: 1px solid $color-grey-100;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.65);
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
    width: 100%;
    max-width: 768px;
    display: flex;
    flex-flow: row wrap;
    position: absolute;
    z-index: 1;
    padding: 1rem 0.5rem;

    max-height: calc(100vh - 17rem);
    overflow: auto;

    &-column {
      display: flex;
      flex-direction: column;
      flex: 33.333%;
      min-width: 200px;
      padding: 0.5rem;

      &--heading {
        font-weight: bold;
        margin-bottom: 1rem;
        margin-top: 1rem;

        @media screen and (min-width: $break-lg) {
          margin-top: 0;
        }
      }
    }

    &-items {
      display: flex;
      flex-direction: column;
    }

    &-item {
      background-color: $color-grey-50;
      border-radius: 4px;
      color: $color-purple;
      margin-bottom: 0.5rem;
      padding: 1rem 0.75rem;

      @media screen and (min-width: $break-lg) {
        margin-bottom: 0.1rem;
        padding: 0.5rem;
      }

      &:hover:not(&--active) {
        background-color: $color-grey-100;
        color: black;
      }

      &--active {
        background-color: $color-blue;
      }
    }
  }
}
