


























































































































































































































































@import '../styles/colors';
@import '../styles/variables';

.version-tag {
  align-self: center;
  padding: 0.25rem 0.5rem;
  border: 1px solid $color-purple;
  border-radius: 1px;
  font-size: $font-small;
  margin: 0 0.5rem;
  font-weight: 500;

  @media screen and (min-width: $break-lg) {
    padding: 0.25rem 0.75rem;
    transform: translateY(-1rem);
  }
}

.navbar {
  background-color: $color-yellow;
  display: flex;
  position: relative;
  flex: 5em 0 0;
  width: 100%;
  z-index: 4;
  padding-left: 0rem;

  @media screen and (min-width: $break-lg) {
    padding-left: 1rem;
    background-color: inherit;
    flex-direction: column;
    padding-bottom: 2em;
    position: fixed;
    width: $sidebarWidth;
    top: 0;
    height: 100vh;
    overflow-y: auto;
  }
}

.navigation {
  position: absolute;
  width: 100%;
  top: 5em;
  right: 0;
  z-index: 1;
  background: white;
  padding: 0 0 2em;
  display: none;
  border-top: 2px solid $color-bg;
  box-shadow: 0 2px 3px rgba($color-grey-600, 0.5);
  max-height: calc(100vh - 5em);
  overflow-y: auto;

  &--show {
    display: block;
  }

  @media screen and (min-width: $break-sm) {
    width: calc(100% - 5.5em);
    border-top: none;
  }

  @media screen and (min-width: $break-lg) {
    background: initial;
    top: 0;
    display: initial;
    position: relative;
    width: 100%;
    padding: 0;
    box-shadow: none;
  }

  &-list {
    padding: 0 1em;

    @media screen and (min-width: $break-lg) {
      padding: 0;
    }
  }
}

.selectedSubpage {
  color: $color-purple;
  font-weight: 500;
  font-size: $font-medium;
  display: block;
  height: auto;
  text-align: left;
  padding-left: 1rem;
  flex: auto 1 0;
  cursor: pointer;

  @media screen and (min-width: $break-lg) {
    display: none;
  }
}

.logo-link {
  display: flex;
  align-self: center;
  flex-basis: auto;
  align-items: center;
  justify-content: center;
  height: 8.5em;
  min-height: 8.5em;
  background: $color-bg;

  @media screen and (min-width: $break-lg) {
    padding: 0.5rem 0;
    background: none;
  }
}

.oslo__logo {
  cursor: pointer;
  object-fit: contain;
  width: 5.5em;
  height: 100%;
  padding: 1.25rem;

  @media screen and (min-width: $break-lg) {
    width: 6.5em;
    align-self: center;
    padding-bottom: 0.75rem;
  }
}

.navigation-link {
  $p: &;

  align-items: center;
  background-color: inherit;
  cursor: pointer;
  display: flex;
  position: relative;
  transition: all 0.3s ease-in-out;
  width: 100%;
  border-radius: 3rem;
  margin-bottom: 1px;

  &--active {
    background-color: $color-yellow;
    transition: all 0.3s ease-in-out;

    #{$p}__label {
      color: $color-purple;
      font-weight: 500;
    }

    label:hover {
      background: none !important;
    }

    input[type='checkbox']:checked + label {
      &::after {
        transform: scale(0);
      }
    }
  }

  &--compare {
    background-color: lighten($color-yellow, 15%);
    transition: background-color 0.3s ease-in-out;

    label::before {
      opacity: 1 !important;
    }

    label::after {
      transform: scale(1) !important;
      transition: transform 0.3s cubic-bezier(0.29, -0.01, 0.41, 1.9);
    }
  }

  &--compare &__label {
    color: $color-purple;
  }

  &__label {
    align-items: center;
    color: rgba($color-purple, 0.85);
    display: flex;
    flex-grow: 1;
    height: 3em;
    letter-spacing: 0.1px;
    position: relative;
    border-top-right-radius: 3rem;
    border-bottom-right-radius: 3rem;

    &-compare {
      margin-bottom: 1rem;
      margin-top: 0rem;
      padding-left: 4rem;

      @media screen and (min-width: $break-md) {
        margin-top: 1rem;
        padding-left: 3.5rem;
      }
    }

    // Add visual border on the left side of text on hover
    &::before {
      background: $color-purple;
      bottom: 0;
      content: '';
      display: none;
      left: -10px;
      pointer-events: none;
      position: absolute;
      top: 0;
      width: 10px;
    }
  }

  &:hover:not(&--active) label:not(.compare)::before {
    transition: all 0s;
    opacity: 0.15;
  }

  &:not(&--active):not(&--compare) &__label:hover {
    background-color: darken($color-bg, 5%);
    color: $color-purple;

    // Show the visual border on hover
    &::before {
      background-color: darken($color-bg, 5%);
      display: block;
    }
  }
}

input[type='checkbox'] {
  display: none;

  & + label {
    cursor: pointer;
    display: inline-block;
    height: 40px;
    padding-left: 1rem;
    position: relative;
    vertical-align: middle;
    width: 50px;
    border-top-left-radius: 3rem;
    border-bottom-left-radius: 3rem;

    &:hover {
      background: darken($color-bg, 10%);

      &::before {
        background: rgba(white, 0.15);
        opacity: 0.8 !important;
      }
    }

    // checked inside border
    &::after {
      background-color: $color-purple;
      border-radius: 1px;
      content: '';
      height: 10px;
      left: 24px;
      position: absolute;
      top: 14px;
      transform: scale(0);
      width: 10px;
    }

    &:not(.compare) {
      &::before,
      &::after {
        transition: all 0.3s ease-in-out;
      }
    }

    // unchecked border
    &::before {
      -moz-border-radius: 1px;
      -webkit-border-radius: 1px;
      border: 1px solid $color-purple;
      border-radius: 1px;
      content: '';
      height: 18px;
      left: 20px;
      opacity: 0;
      position: absolute;
      top: 10px;
      width: 18px;
    }

    &.compare::before {
      opacity: 0.35;
    }
  }

  &:checked:not(&-compare) + label {
    position: relative;
    opacity: 1;

    // checked inside border
    &::after {
      transform: scale(1);
      transition: transform 0.3s cubic-bezier(0.29, -0.01, 0.41, 1.9);
    }

    // checked border
    &::before {
      border: 1px solid $color-purple;
      opacity: 1;
      transition: transform 0.3s ease-in-out;
    }
  }
}

.navigation-drawer__buttons {
  display: flex;
  flex-direction: column;
  padding: 0 1rem;

  .navigation-drawer__button-container {
    display: flex;

    @media screen and (min-width: $break-lg) {
      align-self: flex-end;
    }

    .navigation-drawer__button {
      -moz-border-radius: 5px;
      -webkit-border-radius: 5px;
      border: 1px solid $color-purple;
      border-radius: 5px;
      color: $color-purple;
      font-weight: bold;
      margin: 0.5rem;
      padding: 0.3rem 1rem 0.3rem 1rem;
      height: 42px;
      width: 50%;
      transition: all 0.3s ease-in-out;

      &:not([disabled]) {
        cursor: pointer;
      }

      @media screen and (min-width: $break-md) {
        width: auto;
      }

      @media screen and (min-width: $break-md) {
        height: auto;
      }

      &:disabled {
        opacity: 0.2;
        transition: opacity 0.3s ease-in-out;
      }

      &:hover:not([disabled]) {
        box-shadow: 0 0 0 1px $color-grey-50;
      }
    }
  }
}

.navigation-drawer__select-container {
  display: flex;
  position: relative;

  @media screen and (min-width: $break-lg) {
    align-self: flex-end;
  }

  .navigation-drawer__select {
    border: 1px solid $color-purple;
    color: $color-purple;
    font-weight: bold;
    margin: 0.5rem;
    padding: 0.5rem 1rem 0.5rem 1rem;
    position: relative;
    width: 100%;
    height: 42px;
    cursor: pointer;

    @media screen and (min-width: $break-lg) {
      width: 205px;
      height: auto;
    }
  }

  &:before {
    border-bottom: 2px solid $color-purple;
    border-left: 2px solid $color-purple;
    content: '';
    height: 0.5rem;
    position: absolute;
    top: calc(50% - 0.35rem);
    transform: rotate(-45deg);
    width: 0.5rem;
    right: 1.5rem;
  }
}
