.app {
  display: flex;
  flex-direction: column;
  max-width: 1920px;
  margin: 0 auto; }
  @media screen and (min-width: 1024px) {
    .app {
      flex-direction: row; } }

.app__content {
  width: 100%; }
  @media screen and (min-width: 1024px) {
    .app__content {
      padding-left: 20rem; } }

.main-container {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 10vh; }

.main-container__item {
  padding: 5px;
  margin-top: 10px; }

.main-container__cards {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  min-width: 300px;
  padding: 0;
  flex: 2 1 800px; }
  @media screen and (min-width: 768px) {
    .main-container__cards {
      padding: 0 0.5em; } }
  @media screen and (min-width: 1024px) {
    .main-container__cards {
      padding: 0em 1em; } }

.main-container__map {
  display: flex;
  padding: 1rem;
  flex-flow: row wrap;
  justify-content: flex-start;
  height: 300px;
  min-width: 200px;
  flex: 1 1 300px; }

.topics-grid, .notFound__topics-grid {
  padding: 0 1em;
  flex: 2 1 800px;
  display: grid;
  gap: 1em;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); }
  @media screen and (min-width: 768px) {
    .topics-grid, .notFound__topics-grid {
      padding: 1em; } }
  @media screen and (min-width: 1024px) {
    .topics-grid, .notFound__topics-grid {
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); } }
  @media all and (-ms-high-contrast: none) {
    .topics-grid, .notFound__topics-grid {
      display: flex;
      flex-wrap: wrap; } }

/*
  Reduce opacity on site when district or topic selectors
  are open. Even though district selector dropdown isn't relevant
  on larger screens, the drawer can still be opened while
  the screen is small and then viewport can be changed,
  some media query and pseudo selector (:not) hacks are needed.
*/
.menuIsOpen,
.navigationIsOpen {
  max-height: 100vh;
  overflow-y: hidden; }
  .menuIsOpen main,
  .menuIsOpen .navigation,
  .navigationIsOpen main,
  .navigationIsOpen .navigation {
    opacity: 0.2; }

.navigationIsOpen:not(.menuIsOpen) .navigation {
  opacity: 1; }

@media screen and (min-width: 1024px) {
  .navigationIsOpen:not(.menuIsOpen) {
    max-height: 100vh;
    overflow-y: hidden; }
    .navigationIsOpen:not(.menuIsOpen) main,
    .navigationIsOpen:not(.menuIsOpen) .navigation {
      opacity: 1; } }

.notFound-container {
  display: flex;
  flex-direction: column;
  padding: 0 2rem;
  max-width: 1100px;
  color: #292858; }
  .notFound-container--row {
    display: flex;
    flex-direction: row;
    padding-bottom: 5vh; }
  .notFound-container--column {
    display: flex;
    flex-direction: column;
    padding-bottom: 5vh; }

.notFound__square--outer {
  flex: 150px 0 1; }

.notFound__square--inner {
  background: #f8c66b;
  width: 100%;
  padding-top: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative; }

.notFound__square--label {
  position: absolute;
  top: 50%;
  transform: translateY(-0.7em);
  font-size: 3em;
  color: #292858; }

.notFound__information {
  width: 100%;
  margin: 0 0 0 1rem;
  display: flex;
  flex: 350px 0 1;
  justify-content: space-between;
  flex-direction: column; }
  .notFound__information--header {
    font-size: 1.55rem;
    font-weight: 500;
    margin: 0; }
  .notFound__information--frontpage-link {
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.33);
    background-color: white;
    padding: 0.3rem 1.5rem;
    font-weight: normal;
    font-size: 16px;
    width: 160px; }

.notFound__topics-grid {
  padding: 0; }
